import React, { useEffect, useMemo, useState } from 'react'
import { TYPE } from '../../theme'
import { useTranslation } from 'react-i18next'

const MINUTE = 60
const HOUR = MINUTE * 60
const DAY = HOUR * 24

export function Countdown({
  stakingGenesis,
  rewardsDurationDays,
  exactEnd
}: {
  stakingGenesis: number
  rewardsDurationDays: number
  exactEnd?: Date
}) {
  const REWARDS_DURATION = DAY * rewardsDurationDays

  const { t } = useTranslation()
  // get end/beginning times
  const end = useMemo(() => (exactEnd ? Math.floor(exactEnd.getTime() / 1000) : stakingGenesis + REWARDS_DURATION), [
    REWARDS_DURATION,
    exactEnd,
    stakingGenesis
  ])
  const begin = useMemo(() => end - REWARDS_DURATION, [REWARDS_DURATION, end])

  // get current time
  const [time, setTime] = useState(() => Math.floor(Date.now() / 1000))
  useEffect((): (() => void) | void => {
    // we only need to tick if rewards haven't ended yet
    if (time <= end) {
      const timeout = setTimeout(() => setTime(Math.floor(Date.now() / 1000)), 1000)
      return () => {
        clearTimeout(timeout)
      }
    }
  }, [time, end])

  const timeUntilGenesis = begin - time
  const timeUntilEnd = end - time

  let timeRemaining: number
  let message: string
  if (timeUntilGenesis >= 0) {
    message = t('Rewards begin in')
    timeRemaining = timeUntilGenesis
  } else {
    const ongoing = timeUntilEnd >= 0
    if (ongoing) {
      message = t('Rewards end in')
      timeRemaining = timeUntilEnd
    } else {
      message = t('Rewards have ended!')
      // message = t('Rewards not started yet.')
      timeRemaining = Infinity
    }
  }

  const days = (timeRemaining - (timeRemaining % DAY)) / DAY
  timeRemaining -= days * DAY
  const hours = (timeRemaining - (timeRemaining % HOUR)) / HOUR
  timeRemaining -= hours * HOUR
  const minutes = (timeRemaining - (timeRemaining % MINUTE)) / MINUTE
  timeRemaining -= minutes * MINUTE
  const seconds = timeRemaining

  return (
    <TYPE.black style={{ textAlign: 'right' }} color={'white'} fontWeight={500}>
      {message}{' '}
      {Number.isFinite(timeRemaining) && (
        <code>
          {`${days}:${hours.toString().padStart(2, '0')}:${minutes
            .toString()
            .padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`}
        </code>
      )}
    </TYPE.black>
  )
}
